'use client';

import React, { useState } from 'react';
import { useRouter } from 'next/navigation';
import {
  Container,
  Typography,
  Box,
  Grid,
  CircularProgress,
  Alert,
  IconButton,
} from '@mui/material';
import { ArrowBack as ArrowBackIcon } from '@mui/icons-material';
import { useAuth } from '@/lib/auth-context';
import { useRestaurants } from '@/lib/api/hooks';
import ManagerRestaurantCard from '@/components/restaurant/ManagerRestaurantCard';

const CreateReservationPage: React.FC = () => {
  const router = useRouter();
  const { user } = useAuth();

  // Запросы данных
  const { data: restaurants, isLoading: restaurantsLoading, error: restaurantsError } = useRestaurants();

  // Проверка доступа
  React.useEffect(() => {
    if (user && user.role !== 'manager' && user.role !== 'admin') {
      router.push('/');
    }
  }, [user, router]);

  const handleRestaurantSelect = (restaurantId: number) => {
    router.push(`/manager/reservations/create/${restaurantId}`);
  };

  if (restaurantsLoading) {
    return (
      <Container maxWidth="xl" sx={{ mt: 4 }}>
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
          <CircularProgress />
        </Box>
      </Container>
    );
  }

  if (restaurantsError) {
    return (
      <Container maxWidth="xl" sx={{ mt: 4 }}>
        <Alert severity="error">
          Ошибка при загрузке ресторанов: {restaurantsError.message}
        </Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="xl" sx={{ mt: 4, mb: 4 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
        <IconButton
          onClick={() => router.push('/manager/reservations')}
          sx={{ mr: 2 }}
          color="primary"
        >
          <ArrowBackIcon />
        </IconButton>
        <Typography variant="h4" component="h1">
          Создание бронирования - выберите ресторан
        </Typography>
      </Box>

      <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 3 }}>
        {Array.isArray(restaurants) ? restaurants.map((restaurant: any) => (
          <Box key={restaurant.restaurant_id} sx={{ flex: '1 1 300px', minWidth: '300px', maxWidth: '400px' }}>
            <Card 
              sx={{ 
                height: '100%', 
                cursor: 'pointer',
                transition: 'transform 0.2s, box-shadow 0.2s',
                '&:hover': {
                  transform: 'translateY(-2px)',
                  boxShadow: 4,
                }
              }}
              onClick={() => handleRestaurantSelect(restaurant.restaurant_id)}
            >
              <CardContent>
                <Typography variant="h6" component="h3" gutterBottom>
                  {restaurant.restaurant_name}
                </Typography>
                
                <Typography variant="body2" color="text.secondary" paragraph>
                  {restaurant.restaurant_description}
                </Typography>

                <Box sx={{ mt: 2 }}>
                  <Typography variant="body2" color="text.secondary">
                    <strong>Адрес:</strong> {restaurant.city}, {restaurant.street_address}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    <strong>Рейтинг:</strong> {restaurant.rating ? `${restaurant.rating}/5` : 'Нет оценок'}
                  </Typography>
                </Box>

                <Box sx={{ mt: 2, display: 'flex', justifyContent: 'center' }}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleRestaurantSelect(restaurant.restaurant_id);
                    }}
                  >
                    Выбрать ресторан
                  </Button>
                </Box>
              </CardContent>
            </Card>
          </Box>
        )) : null}

        {(!restaurants || restaurants.length === 0) && (
          <Box sx={{ width: '100%' }}>
            <Card>
              <CardContent>
                <Typography variant="h6" color="text.secondary" align="center" gutterBottom>
                  Рестораны не найдены
                </Typography>
                <Typography variant="body2" color="text.secondary" align="center">
                  В системе пока нет ресторанов для бронирования
                </Typography>
              </CardContent>
            </Card>
          </Box>
        )}
      </Box>
    </Container>
  );
};

export default CreateReservationPage;